import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/about',
    name: 'About',
    component: () => import( '../views/About.vue')
  },
  {
    path: '/mostpopular',
    name: 'mostpopular',
    component: () => import( '../views/mostpopular.vue')
  },
  {
    path: '/top',
    name: 'top',
    component: () => import( '../views/top.vue')
  },
  {
    path: '/homeresult',
    name: 'homeresults',
    component: () => import( '../views/homeresults.vue')
  }
]

const router = new VueRouter({
  routes
})

export default router
